body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: aliceblue;
  background-image: linear-gradient(to left, #004488 0%, #00AACC 100% );
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h1{
  display: block;
  width: 100%;
  text-align: center;
}

.container, .results{
  font-size: large;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 15px;
}

.container{
  display: block;
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
  padding: 25px;
  width: 20%;
  height: 40vh;
}

.results{
  position: absolute;
  left: 15px;
  top: 20%;
  width: 250px;
  padding: 15px;
  text-align: center;
}


input{
  display: block;
  margin-top: 15px;
  margin-bottom: 5px;
  
}

p{
  font-size: x-large;
  
}

.imcresult{
  color: rgb(19, 20, 20);
  font-weight: 500;
}
